<template>
  <div class="card">
    <div class="card-header">Exam02EventEmit</div>
    <div class="card-body">
      <Child
        @child-event-1="handleChlidEvent1"
        @child-event-2="handleChildEvent2"
        :counter="counter"
        @increment-event="counter++"
        @decrement-event="handleDecrementEvent"
      />
    </div>
  </div>
</template>

<script setup>
import Child from "./Child.vue";
import { ref } from "vue";

//최초값은 부모가 갖고 있고, counter값을 바꾸는 주체도 부모여야 함.
//child에선 counter값을 바꿔달라는 요청을 할 뿐임.
//그렇게 매번 바뀌는 값을 prop으로 child에게 넘겨주는 식임.
const counter = ref(0);

function handleDecrementEvent() {
  counter.value--;
}

function handleChlidEvent1(data1) {
  console.log("data1:", data1);
}
function handleChildEvent2(data1, data2) {
  console.log("data1:", data1);
  console.log("data2:", data2);
}
</script>

<style scoped></style>
