<template>
  <div class="card">
    <div class="card-header">Child</div>
    <div class="card-body">
      <div>
        <h6>[자식 -> 부모]</h6>
        <button @click="$emit('child-event-1', data1)" class="btn btn-info btn-sm mr-2">emit child-event-1</button>
        <button @click="handleChildEmit2" class="btn btn-info btn-sm mr-2">emit child-event-2</button>
      </div>
      <hr />
      <div>
        <h6>[자식(event)-> 부모(props)-> 자식]</h6>
        <button @click="handleIncrementEvent" class="btn btn-info btn-sm mr-2">emit increment-event</button>
        <button @click="handleDecrementEvent" class="btn btn-info btn-sm mr-2">emit decrement-event</button>
        <p>Counter: {{ counter }}</p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineEmits, defineProps } from "vue";
const data1 = ref("value1");
const data2 = ref(100);

const emit = defineEmits(["child-event-2", 'increment-event', 'decrement-event']);
const props = defineProps(['counter'])

function handleChildEmit2() {
  emit("child-event-2", data1.value, data2.value);
}
function handleIncrementEvent() {
  emit("increment-event"); // 단순히 ++ 또는 -- 하는 이벤트니까 전달데이터 안 적을거임
}
function handleDecrementEvent() {
  emit("decrement-event");
}
</script>

<style scoped></style>
